$mainColour: #353535;

.main {
  display: flex;
  flex-direction: column;
  align-items: center;
  max-width: 1000px;
  width: 95%;
}

.todoWrapper {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;

  font-size: 1.15rem;

  padding: 8px;
  margin: 12px 0px;

  border-radius: 10px;
  background: #ececec;
  box-shadow: 2px 2px 4px rgba(0, 0, 0, 0.2);

  border: solid 1px rgba(73, 165, 240, 0);

  transition: all;
  transition-duration: 0.2s;

  .icon {
    width: 24px;
    min-width: 24px;
    height: 24px;

    display: flex;
    justify-content: center;
    align-items: center;
    user-select: none;

    margin: 0 12px;
  }

  .title {
    flex: 10;
    text-align: left;
    padding-left: 5px;
    font-weight: bold;
    user-select: none;
  }

  .priority {
    user-select: none;
    width: 26px;
    min-width: 26px;
    height: 26px;
    margin: 0 12px;
    color: inherit;
    border: solid 1px $mainColour;

    display: flex;
    justify-content: center;
    align-items: center;

    font-size: 14px;
    border-radius: 50%;
  }

  .completedButton {
    font-weight: bold;
    transition-property: transform, opacity, color, border-color;
    transition-duration: 0.3s;
    opacity: 0.2;
    border-width: 2px;

    &:hover {
      // color: rgb(73, 165, 240);
      // border-color: rgb(73, 165, 240);
      cursor: pointer;
      transform: scale(1.1);
    }
  }

  .editIcon {
    padding: 4px;
    opacity: 0.2;
    width: 26px;
    fill: inherit;
    user-select: none;
    transition-property: transform, fill, opacity;
    transition-duration: 0.3s;

    &:hover {
      // fill: rgb(73, 165, 240);
      cursor: pointer;
      transform: scale(1.1);
    }
  }

  &:hover {
    border-radius: 10px;
    box-shadow: 5px 5px 4px rgba(0, 0, 0, 0.3);

    .editIcon,
    .completedButton {
      opacity: 1;
    }
  }
}

@media only screen and (max-width: 400px) {
  .todoWrapper {
    .icon {
      margin: 0 2px;
    }
    .priority {
      margin: 0 4px;
    }
  }
}
